@font-face {
	font-family: "pirulen_rg";
	src: url("../assets/font/Pirulen/pirulen_rg.ttf");
}

i[class^="icon-"],
i[class*=" icon-"] {
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center center;
}

.icon-header-logo {
	width: 128px;
	height: 34px;
	background-image: url("../assets/images/header_logo.png");
	margin-left: 19px;
}

.icon-header-nav {
	width: 20px;
	height: 20px;
	background-image: url("../assets/images/header_nav.png");
	margin-left: 33px;
}

.icon-input-search {
	width: 20px;
	height: 20px;
	background-image: url("../assets/images/input_search.png");
}

.icon-example-info {
	width: 15px;
	height: 15px;
	background-image: url("../assets/images/example_info.png");
}

.icon-google {
	width: 15px;
	height: 15px;
	background-image: url("../assets/images/icon_google.png");
}

.icon-help {
	width: 15px;
	height: 15px;
	background-image: url("../assets/images/icon_help.png");
}
